import React, { useState, useEffect } from 'react'
import { FaBars } from 'react-icons/fa';
import { Nav, NavbarContainer, NavLogo, MobileIcon, NavMenu, NavItme, NavLinks } from './NavebarElements';
import {IconContext} from 'react-icons/lib';
import { animateScroll as scroll } from 'react-scroll';
const Navbar = ({toggle}) => {

    const [scrollNav, setScrollNav] = useState(false)

    const changeNav = () => {
        if(window.scrollY >= 80) {
            setScrollNav(true);
        } else {
            setScrollNav(false);
        }
    };

    useEffect( () => {
        window.addEventListener('scroll', changeNav);
    }, []);

    const toggleHome = () =>{
        scroll.scrollToTop();
    };
    return (
        <>
        <IconContext.Provider value={{color: '#fff'}} >


            <Nav scrollNav={scrollNav}>
                <NavbarContainer>
                     <NavLogo to='/' onClick={toggleHome} > dolla </NavLogo>
                     <MobileIcon onClick = {toggle}>
                         <FaBars />
                     </MobileIcon>
                     <NavMenu>
                        <NavItme>
                            <NavLinks to='about' smooth={true} duration={500} spy={true} exact='true' offset={-80}  >About</NavLinks>
                        </NavItme>
                        <NavItme>
                            <NavLinks to='contact'  smooth={true} duration={500} spy={true} exact='true' offset={-80} >Contact</NavLinks>
                        </NavItme>
                        <NavItme>
                            <NavLinks to='services'  smooth={true} duration={500} spy={true} exact='true' offset={-80} >Services</NavLinks>
                        </NavItme>
                        <NavItme>
                            <NavLinks to='signup'  smooth={true} duration={500} spy={true} exact='true' offset={-80} >Sign up</NavLinks>
                        </NavItme> 
                     </NavMenu>
                  {/*  <NavBtn>
                         <NavBtnLink to = "/signin"> Sign In </NavBtnLink>
                  </NavBtn> 
                  
                  , NavBtn, NavBtnLink in import siction
                  
                  */}
                </NavbarContainer>
            </Nav>
            </IconContext.Provider>
        </>
    )
}

export default Navbar
